@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";

section.sections {
    padding: 30px 0;
}

.sections {

    .container {
        max-width: 1200px;
    }

    .section-h1 {
        margin-bottom: 40px;
        padding: 0 16px;
        text-align: center;
    }

    .swiper {
        overflow: visible;

        margin: 0 auto;
    }

    &-pagination {
        position: relative;
        padding-top: 36px;

        .swiper-pagination {
            position: static;
        }
    }

    .swiper-wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .swiper-slide {
        display: flex;
        flex-direction: column;
        height: auto;
        overflow: hidden;
        max-width: 100%;
        width: calc(50% - 12px);
        margin-bottom: 24px;

        @media screen and (max-width: 960px) {
            width: 100%;
        }

        &:hover {

            .image {
                border-radius: 24px;
            }

        }

        .image {
            position: relative;
            padding-top: 50%;
            background: #fff;
            transition: all cubic-bezier(0.4, 0, 0.2, 1) 250ms;
            border-radius: 64px;
            overflow: hidden;



            img {
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                top: 0;
                height: 100%;
                width: 100%;
                object-fit: cover;
                object-position: center;
            }
        }

        .title {
            font-size: 32px;
            line-height: 40px / 32px * 100%;
            padding: 0 16px;
            color: #1F1F1F;
            margin-top: 16px;
            margin-bottom: 8px;

            @media screen and (max-width: 480px) {
                font-size: 28px;
                line-height: 110%;
            }

            @media screen and (max-width: 360px) {
                font-size: 24px;
            }
        }

        .desc {
            font-size: 16px;
            line-height: 24px / 16px * 100%;
            padding: 0 16px;
            padding-bottom: 16px;
            color: #444746;
        }

        .goto {
            font-weight: 600;
            padding: 16px 16px 27px;
            margin-top: auto;
            display: none;
            align-items: center;

            svg {
                margin-left: 10px;
            }

            span {
                color: #0b57d0;
                border-bottom: 1px solid transparent;
                transition: all 0.25s ease;
            }

            span:hover {
                color: #041E49;
                border-bottom: 1px solid #041E49;
            }
        }
    }
}